@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700&display=swap);
:root{--btn-next: #444;--light-blue: #84c2ff;--orange: #ffd084;--dark-orange: #ff9284;--main-bg: #fafafa;--secondary-bg: #eee;--label: #616161;font-family:Roboto, 'sans-serif'}[data-theme='dark']{--btn-next: #000;--light-blue: #444;--orange: #000;--dark-orange: #444;--main-bg: #616161;--secondary-bg: #aaa;--label: #fff}*,:before,:after{box-sizing:border-box;margin:0px;padding:0px}.App{background-color:#eee;background-color:var(--secondary-bg)}.heading{margin-bottom:40px;text-decoration:underline;color:#616161;color:var(--label)}.wrapper{display:flex;width:100%;height:100vh;max-width:600px;flex-direction:column;align-items:center;justify-content:center;margin:0 auto;padding:0px 15px;background-color:#fafafa;background-color:var(--main-bg)}.wrapper__btn{display:flex;justify-content:space-between;margin-top:20px}.form{width:100%;max-width:400px}.form__label{color:#616161;color:var(--label);display:flex;flex-direction:column;position:absolute;width:100%;padding-top:10px}.form__input-wrapper{position:relative;margin-bottom:140px}.form__input{margin-top:10px;padding:10px;border:1px solid var(--btn-prev);border-radius:3px}.form__error{position:absolute;top:80px;font-size:11px;left:5px;color:#ff9284;color:var(--dark-orange)}.btn{border:none;width:160px;height:38px;color:#fff;font-size:18px;font-weight:300;border-radius:6px}.btn__next{background-color:#84c2ff;background-color:var(--light-blue)}.btn__prev{background-color:#ff9284;background-color:var(--dark-orange)}.btn__next:hover,.btn__prev:hover{-webkit-filter:brightness(125%);filter:brightness(125%);cursor:pointer}.legend__wrapper{width:100%;display:flex;justify-content:space-around;margin-top:10px;margin-bottom:20px}.legend__indicator{border-radius:50%;border:1px solid #84c2ff;border:1px solid var(--light-blue);width:40px;height:40px;display:flex;align-items:center;justify-content:center;font-size:18px;font-weight:300;background-color:#84c2ff;background-color:var(--light-blue);color:#fff}.legend__indicator.active{background-color:#ffd084;background-color:var(--orange)}.cookie-data{margin:0 auto;border-radius:5px;border:1px dashed #616161;border:1px dashed var(--label);padding:20px;margin-top:40px;font-size:14px;line-height:22px;font-weight:300}.thankYouPage,.spinner{width:100vw;height:100vh;background-color:#fff;color:#616161;color:var(--label);top:0;left:0;position:absolute;display:flex;flex-direction:column;justify-content:center;align-items:center;z-index:999}.thankYouPage .loader,.spinner .loader{font-size:10px;margin:50px auto;text-indent:-9999em;width:11em;height:11em;border-radius:50%;background:#00d1ff;background:-webkit-gradient(linear, left top, right top, color-stop(10%, #00d1ff), color-stop(42%, rgba(0,209,255,0)));background:linear-gradient(to right, #00d1ff 10%, rgba(0,209,255,0) 42%);position:relative;-webkit-animation:load3 1.4s infinite linear;animation:load3 1.4s infinite linear;-webkit-transform:translateZ(0);transform:translateZ(0)}.thankYouPage .loader:before,.spinner .loader:before{width:50%;height:50%;background:#00d1ff;border-radius:100% 0 0 0;position:absolute;top:0;left:0;content:''}.thankYouPage .loader:after,.spinner .loader:after{background:#fcfcfc;width:75%;height:75%;border-radius:50%;content:'';margin:auto;position:absolute;top:0;left:0;bottom:0;right:0}@-webkit-keyframes load3{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes load3{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.thankYouPage{background-color:#fafafa;background-color:var(--main-bg)}.hidden{visibility:hidden}.theme-switch{display:inline-block;height:24px;position:relative;width:40px}.theme-switch__wrapper{display:flex;align-items:center;position:absolute;top:40px;left:50%;margin-left:-80px}.theme-switch__wrapper em{margin-left:10px;font-size:14px;color:#616161;color:var(--label)}.theme-switchinput{display:none}.slider{background-color:#ccc;bottom:0;cursor:pointer;left:-5px;position:absolute;right:5px;top:0;-webkit-transition:0.4s;transition:0.4s}.slider:before{background-color:#fff;bottom:4px;content:'';height:16px;left:4px;position:absolute;-webkit-transition:0.4s;transition:0.4s;width:16px}input:checked+.slider{background-color:#66bb6a}input:checked+.slider:before{-webkit-transform:translateX(16px);transform:translateX(16px)}.slider.round{border-radius:34px}.slider.round:before{border-radius:50%}

